.categoryWidget {
    min-width: 105px;
    height: 177px;
    background: rgb(29, 29, 29);
    border-radius: 20px;
    filter: drop-shadow(0px 2px 10px rgba(0, 0, 0, 0.308));
}

.categoryImg {
    display: block;
    margin-top: 24px;
    margin-left: auto;
    margin-right: auto;
    width: 60px;
    height: 60px;
}

.categoryTitle {
    display: block;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    font-size: 14px;    
    color: white;
    text-align: center;
}

.categoryAngle {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    width: 26px;
    height: 26px;
    border-radius: 50%;
}

.categoryAngle .categoryAngleIcon {
    width: 26px;
    height: 26px;
}


.categoryWidget:hover {
    background-color: #aeaeae;
    color: #ffffff;

}
.categoryWidget:hover > .categoryAngle{
    background-color: #ffffff;

}

.categoryWidget:hover > .categoryTitle{
    color: #ffffff;
}
.categoryWidget:hover > .categoryAngleIcon{
    color: white !important;
}