.topbarContainer{
    height: 50px;
    width: 100%;
    background-color: #281E41;
    display: flex;
    align-items: center;
    position: static;
    top: 0;
}

.topbarLeft{
    flex: 3;
}
.logo{
    font-style: 24px;
    margin-left: 20px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}

.topbarCenter{
    flex: 5;
}

.searchbar{
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 30px;
    display: flex;
    align-items: center;
}

.searchIcon{
    font-size: 20px !important;
    margin-left: 10px;
}

.searchInput{
    border: none;
    width: 70%;
}

.searchInput:focus{
    outline: none;
}

.topbarRight{
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
}

.topbarLinks{
    margin-right: 10px;
    font-size: 14px;
    cursor: pointer;
}

.topbarIcons{
    display: flex;
}

.topbarIconItem{
    margin-right: 15px;
    cursor: pointer;
    position: relative;
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    justify-content: center;
    font-size: 12px;
}

.topbarImg{
    height: 32px;
    width: 32px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
    display: flex;
}

.topbarProfile{
    display: inline;
    float: right;
    text-align: right;
    cursor: pointer;
}

.topbarProfileName{
    font-style: 11px;
    display: flex;
    align-items: center;
}

.topbarProfileInfo{
    font-size: 10px;
    display: flex;
    align-items: center;
    float: right;
}
