.categoryProduct{
    width: 336px;
    height: 300px;
    background: rgb(29, 29, 29);
    border-radius: 20px;
    filter: drop-shadow(0px 2px 10px rgba(0, 0, 0, 0.308));
}

.category-item-img{
    display: block;
    width: 336px;
    height: 170px;
    border-radius: 20px;
    object-fit: cover;
}
.category-item-lower{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
}
.category-item-title{
    display: block;
    font-size: 1.2em;
    font-weight: bold;
    text-align: center;
    color: white;
}
.category-item-price{
    display: block;
    font-size: 1.2em;
    font-weight: bold;
    text-align: center;
    color: white;
}
.category-item-description{
    display: block;
    font-size: 10px;
    font-weight: 600;
    text-align: center;
    color: white;
}
